<!--  -->
<template>
  <van-row class="dt-receive-box">
    <div class="dt-headPortrait-box">
      <headPortrait :username="username"
                    size="small"></headPortrait>
    </div>
    <!-- 图片消息 -->
    <div v-if="icoType === 1">
      <el-image style="width:5rem;height:5rem;border-radius:3%;"
                fit="cover"
                :src="imgSrc"
                :preview-src-list="[imgSrc]"></el-image>
    </div>
    <!-- 视频消息 -->
    <div v-else-if="icoType === 7">
      <video style="width:5rem;height:5rem;border-radius:3%;"
             controls>
        <source :src="videoSrc">
        视频加载失败
      </video>
    </div>
    <div v-else
         class="dt-message-box">
      <!-- <div class="dt-name">{{username}}</div> -->
      <div class="dt-message">{{message}}
        <div v-if="icoType===2"
             class="dt-icon-box1">
        </div>
        <div v-if="icoType===3"
             class="dt-icon-box2">
        </div>
        <div v-else>
        </div>
      </div>
    </div>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import headPortrait from "@/components/headPortrait/headPortrait";
import { IM_BASE_API } from "@/utils/ImConfig";
export default {
  /**username: 'user1' // 用户名
 * message: '消息', // 消息
 * icoType: '1', // 展示图标的类型(0.不显示图标 2.视频通话,3.语音电话) */
  props: ['username', 'message', "icoType", "msgType"],
  //import引入的组件需要注入到对象中才能使用
  components: {
    headPortrait,
  },
  data () {
    //这里存放数据
    return {
      imgSrc: "",
      videoSrc: '',
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    if (this.icoType === 1) {
      this.imgSrc = `${IM_BASE_API}/scooper-app-msg/file/download?uid=${JSON.parse(localStorage.getItem("userInfo")).uid}&id=${this.message}`
    } else if (this.icoType === 7) {
      this.videoSrc = `${IM_BASE_API}/scooper-app-msg/file/download?uid=${JSON.parse(localStorage.getItem("userInfo")).uid}&id=${this.message}`
    }
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {

  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import "../../styles/dimens";
.dt-receive-box {
  display: flex;
  flex-direction: row-reverse;
  align-content: stretch;
  width: 100%;
  .dt-headPortrait-box {
    margin-left: px2rem(15);
  }
  .dt-message-box {
    padding-top: px2rem(3);
    width: 71.2%;
    float: right;
    .dt-name {
      font-size: px2rem(8);
      text-align: right;
    }
    .dt-message {
      font-size: px2rem(15);
      text-align: left;
      line-height: px2rem(30);
      float: right;
      position: relative;
      margin-right: px2rem(0);
      background: #ffffff;
      border-radius: 5px;
      padding-left: px2rem(7.5);
      padding-right: px2rem(7.5);
    }
    .dt-message:before {
      content: "";
      width: 0;
      height: 0;
      position: absolute;
      top: 5px;
      right: -16px;
      border: solid 8px;
      border-color: transparent transparent transparent #ffffff;
      font-size: 0;
    }
    .dt-icon-box1 {
      float: left;
      margin-top: px2rem(5);
      margin-right: px2rem(10);
      width: px2rem(20);
      height: px2rem(20);
      border-radius: px2rem(5);
      background-image: url("../../assets/img/视频通话.png");
      background-size: 100%;
      background-repeat: no-repeat;
    }
    .dt-icon-box2 {
      float: left;
      margin-top: px2rem(5);
      margin-right: px2rem(10);
      width: px2rem(20);
      height: px2rem(20);
      border-radius: px2rem(5);
      background-image: url("../../assets/img/电话.png");
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }
}
</style>