<!-- 聊天 -->
<template>
  <van-row class="dt-chat">
    <!-- 顶部蓝色框首页 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="3">
        <!--返回键-->
        <van-image fit="contain"
                   class="dt-back"
                   :src="require('@/assets/img/返回键.png')"
                   onclick="history.back(-1);" />
      </van-col>
      <van-col span="18">
        <!--会话名称-->
        <van-popover v-model="showPopover"
                     trigger="click">
          <div style="padding: 10px">
            {{ conversationName | filtName }}
          </div>
          <template #reference>
            <div class="dt-chat-name">{{ conversationName | filtName }}</div>
          </template>
        </van-popover>
      </van-col>
    </van-row>
    <van-row ref="content"
             :class="showBox?'dt-content2':'dt-content'"
             @click="table">
      <van-pull-refresh v-model="refreshing"
                        @refresh="onRefresh"
                        class="dt-refresh"
                        success-duration="600">
        <van-list id="cl"
                  v-model="loading"
                  :finished="finished"
                  direction="up"
                  offset="10"
                  @load="onLoad"
                  :error.sync="error"
                  error-text="请求失败，点击重新加载">
          <van-cell class="dt-list"
                    v-for="(item,index) in msgList"
                    :key="item.id">
            <div class="sendTime">{{getSendTime(item.send_time, index)}}</div>
            <sendCard v-if="item.sender===myId"
                      :username="item.sender_name"
                      :message="item.content"
                      :icoType="item.type"></sendCard>
            <receiveCard v-else
                         :username="item.sender_name"
                         :message="item.content"
                         :icoType="item.type"></receiveCard>
            <div v-if="index == 19"
                 id="last"></div>
          </van-cell>
        </van-list>
      </van-pull-refresh>
    </van-row>
    <van-row class="dt-bottom">
      <van-col span="20"
               class="dt-input-box">
        <van-field class="dt-input"
                   type="textarea"
                   @input="input"
                   @focus="focus"
                   v-model="text" />
      </van-col>
      <van-col span="4"
               v-show="isShow"
               class="dt-more-box">
        <van-image fit="contain"
                   class="dt-more"
                   :src="require('../../assets/img/gengduo.png')"
                   @click="box" />
      </van-col>
      <van-col span="4"
               v-show="!isShow"
               class="dt-more-box">
        <button class="dt-send"
                @click="handleSend">发送
        </button>
      </van-col>
    </van-row>
    <van-row v-show="showBox"
             class="dt-box">
      <van-row type="flex"
               justify="space-around">
        <van-col :span="8">
          <van-uploader :after-read="upImg"
                        accept="image/*;video/*">
            <van-image class="dt-ico"
                       @click="videoCall"
                       :src="require('../../assets/img/icon_pic.png')" />
            <p>相册</p>
          </van-uploader>
        </van-col>
        <van-col :span="8">
          <van-image class="dt-ico"
                     @click="audioCall"
                     :src="require('../../assets/img/icon_phone.png')" />
          <p>语音通话</p>
        </van-col>
        <van-col :span="8">
          <van-image class="dt-ico"
                     @click="videoCall"
                     :src="require('../../assets/img/icon_video.png')" />
          <p>视频通话</p>
        </van-col>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import receiveCard from "@/components/chat/receiveCard"
import sendCard from "@/components/chat/sendCard"

import {
  // 获取会话信息
  getConversationInfo,
  // 阅读消息
  readMsg,
  // 获取聊天消息
  getMsgs,
  // 获取个人信息
  getInfoByUid
} from "@/api/message";
// 发送消息
import {
  sendMsg,
  sendFileMsg
} from "@/api/message";
import { Toast } from 'vant';

export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    receiveCard,
    sendCard,
  },
  filters: {
    filtName (name) {
      if (name.length > 4 && name.slice(0, 4) == "多人聊天") {
        return '多人群组'
      } else {
        return name
      }
    }
  },
  data () {
    return {
      // 会话组名
      conversationName: '',
      // 是否为群组
      isGroup: false,
      // 是否发生错误
      error: false,
      // 是否重新刷新
      refreshing: false,
      // 是否正在加载
      loading: false,
      // 是否完成
      finished: false,
      // 自己的id
      myId: '',
      // 自己的用户名
      myUsername: '',
      //输入框
      text: '',
      //底部弹出层
      show: false,
      // 消息列表
      msgList: [],
      // 群友列表
      userList: [],
      isShow: true,
      showBox: false,
      // 消息请求的页数
      pageNumber: 1,
      // 上次内容高度
      messageHeight: 0,

      showPopover: false
    };
  },
  //监听属性 类似于data概念
  computed: {},
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    // 监听事件总线的消息渲染
    this.$bus.on('randerMessage', this.randerMessage);
    // 获取自己的id
    this.getUserInfoByUid();
    // 获取会话信息
    this.getConversationInfo();
    console.log("会话id", this.$route.query.id, typeof this.$route.query.id);
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {
    let contentBox = document.getElementsByClassName("dt-refresh")[0];
    contentBox.onscroll = function () {
    }

    // 阅读全部消息
    this.readMsg()
  },
  beforeCreate () {
  }, //生命周期 - 创建之前
  beforeMount () {
  }, //生命周期 - 挂载之前

  //生命周期 - 更新之前
  beforeUpdate () {
  },
  //生命周期 - 更新之后
  updated () {
  },
  beforeDestroy () {
    // 清除提示
    Toast.clear()
    // 解绑消息总线
    this.$bus.off('randerMessage', this.randerMessage);
  }, //生命周期 - 销毁之前
  destroyed () {
  }, //生命周期 - 销毁完成
  activated () {
  }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {
    readMsg () {
      let query = {};
      query.id = this.$route.query.id
      query.messageId = this.$route.query.lastId
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      readMsg(query).then(() => {
      })
    },
    //发送
    async handleSend () {
      let query = {};
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      query.receivers = this.$route.query.id;
      // 文本消息
      query.messageType = 0;
      query.content = this.text;
      await sendMsg(query).then(res => {
        // 消息发送成功
        if (res.code === 0) {
          // 将消息渲染上去
          this.msgList.push({
            content: this.text,
            sender: this.myId,
            sender_name: this.myUsername
          });
          this.setScroll()
        } else {
          Toast({
            message: '暂不支持表情',
            position: 'top',
          });
        }
      });
      this.text = ''
      this.isShow = true
    },
    upImg (file) {
      if (file instanceof Array) {
        file.forEach(item => {
          this.sendImg(item)
        })
      } else {
        this.sendImg(file)
      }
    },

    sendImg (file) {
      let type = null
      if (file.file.type == "image/png" || file.file.type == "image/jpeg") {
        type = 0
        // ios 视频格式为 video/quicktime
      } else if (file.file.type == "video/mp4" || file.file.type == "video/quicktime") {
        type = 4
      } else {
        Toast("文件格式错误")
        return
      }
      let formData = new FormData()
      formData.append('receivers', this.$route.query.id)
      formData.append('type', type)
      formData.append('file', file.file)
      Toast.loading({
        message: '文件正在上传',
        forbidClick: true,
        duration: 0,
      });
      sendFileMsg(formData).then(res => {
        Toast.clear()
        if (res.data.fileType == 0) {
          this.msgList.push({
            content: res.data.fileId,
            sender: this.myId,
            sender_name: this.myUsername,
            type: 1
          });
        } else if (res.data.fileType == 4) {
          this.msgList.push({
            content: res.data.fileId,
            sender: this.myId,
            sender_name: this.myUsername,
            type: 7
          });
        }
        this.setScroll()
      }).catch(() => {
        Toast.fail({
          message: '上传失败，文件太大！',
        })
      })
    },

    fileOverSize () {
      Toast.fail('文件太大！')
    },

    // 获取聊天消息
    onLoad () {
      if (this.refreshing) {
        this.msgList = [];
        this.userList = [];
        this.refreshing = false;
      }
      let query = {};
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      query.objectId = this.$route.query.id;
      query.pageNumber = this.pageNumber
      query.pageSize = 20
      getMsgs(query).then(res => {
        if (res.code === 0) {
          this.msgList = [...res.list.reverse(), ...this.msgList];

          res.members.forEach(s => {
            this.userList.push({ "tel": s.tel, "name": s.name });
          });
          // 判断是否为第一次请求，如果是第一次则将滚动条设置到最底部
          if (this.pageNumber == 1) {
            this.setScroll()
          } else {
            let contentBox = document.getElementsByClassName("dt-refresh")[0];
            contentBox.scrollTop = contentBox.scrollHeight - this.messageHeight;
            this.messageHeight = contentBox.scrollHeight
          }
          // 如果列表长度大于总条数，或者请求页数大于总页数，则说明数据全部请求回来
          if (this.msgList.length >= res.totalRow || this.pageNumber >= res.totalPage) {
            // 数据全部加载完成
            this.finished = true;
          } else {
            // 否则再次触底时请求下一页
            this.pageNumber += 1
            this.finished = false;
          }
          // 每次数据请求回来都要将加载状态设为false
          this.loading = false;
        } else {
          this.error = true
        }
      })
    },
    //语音通话
    audioCall () {
      this.$dialog.confirm({
        message: '是否拨打语音电话',
      }).then(() => {
        // on confirm
        let query = {};
        query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
        query.receivers = this.$route.query.id;
        // 语音
        query.messageType = 3;
        query.content = "发起语音通话";
        sendMsg(query).then(res => {
          // 消息发送成功
          if (res.code === 0) {
            // 将消息渲染上去
            this.msgList.push({
              content: '发起语音通话',
              sender: this.myId,
              sender_name: this.myUsername,
              type: 3
            });
            this.setScroll()
            if (!this.isGroup) {
              if (window.android) {
                window.android.voiceChat(this.userList[0].tel);
              } else if (window.webkit) {
                let sender = this.userList[0].tel;
                window.webkit.messageHandlers.makeAudioCall.postMessage(sender);
              }
            } else {
              if (window.android) {
                window.android.voiceGroupChat(JSON.stringify(this.userList).toString());
              } else if (window.webkit) {
                let sender = JSON.stringify(this.userList).toString();
                window.webkit.messageHandlers.makeAudioGroupCall.postMessage(sender);
              }
            }
            /*if (!this.isGroup) {
              window.android.voiceChat(this.userList[0].tel)
            } else {
              window.android.voiceGroupChat(JSON.stringify(this.userList).toString());
            }*/
          }
        });
      }).catch(() => {
        // on cancel
      });
    },

    //视频通话
    videoCall () {
      this.$dialog.confirm({
        message: '是否拨打视频电话',
      })
        .then(() => {
          let query = {};
          query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
          query.receivers = this.$route.query.id;
          // 视频
          query.messageType = 2;
          query.content = "发起视频通话";
          sendMsg(query).then(res => {
            // 消息发送成功
            if (res.code === 0) {
              // 将消息渲染上去
              this.msgList.push({
                content: '发起视频通话',
                sender: this.myId,
                sender_name: this.myUsername,
                type: 2
              });
              this.setScroll()
              if (!this.isGroup) {
                if (window.android) {
                  window.android.videoChat(this.userList[0].tel)
                } else if (window.webkit) {
                  let sender = this.userList[0].tel;
                  window.webkit.messageHandlers.makeVideoCall.postMessage(sender)
                }
              } else {
                if (window.android) {
                  window.android.videoGroupChat(JSON.stringify(this.userList).toString());
                } else if (window.webkit) {
                  let sender = JSON.stringify(this.userList).toString();
                  window.webkit.messageHandlers.makeVideoGroupCall.postMessage(sender);
                }
              }
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 获取会话组信息
    getConversationInfo () {
      let query = {};
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      query.conversationId = this.$route.query.id;
      getConversationInfo(query).then(res => {
        if (res.code === 0) {
          this.conversationName = res.data.name;
          if (res.data.group_chat === 0) {
            this.isGroup = false;
          } else if (res.data.group_chat === 1) {
            this.isGroup = true;
          }
        }
      });
    },
    // 渲染消息，由消息总线触发
    randerMessage (message) {
      console.log("message", message);
      // this.msgList.push(message);
      // this.msgList.push({
      //   content: this.text,
      //   sender: this.myId,
      //   sender_name: this.myUsername
      // });
    },
    // 下拉刷新
    onRefresh () {
      // 清空列表数据
      this.finished = false;
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      // 从第一页开始请求
      this.pageNumber = 1
      this.onLoad();
    },
    getUserInfoByUid () {
      let query = {};
      query.uid = JSON.parse(localStorage.getItem("userInfo")).uid;
      query.searchUid = JSON.parse(localStorage.getItem("userInfo")).uid;
      getInfoByUid(query).then(res => {
        this.myId = res.data.id;
        this.myUsername = res.data.accShowname;
      });
    },
    // 将滚动条定位到最底部
    setScroll () {
      this.$nextTick(() => {
        let contentBox = document.getElementsByClassName("dt-refresh")[0];
        // let list = document.getElementById("cl");
        contentBox.scrollTop = contentBox.scrollHeight;
      })
    },
    //输入框焦点
    focus () {
      this.showBox = false
      this.setScroll()
    },
    //
    table () {
      this.showBox = false
    },
    //底部弹出层
    box () {
      this.showBox = !this.showBox
      this.setScroll()
    },
    //发送按钮切换
    input () {
      this.isShow = this.text.length <= 0;
    },
    getSendTime (date, index) {
      if (index == 0) {
        return this.timeFormat(date)
      }
      let lastDate = this.msgList[index - 1].send_time
      lastDate = new Date(lastDate).getTime()
      let nowDate = new Date(date).getTime()
      // 如果据上一条消息时间超过5分钟，则展示时间
      let time = ''
      if (nowDate - lastDate > 1000 * 60 * 5) {
        time = this.timeFormat(date)
      } else {
        time = ''
      }
      return time
    },
    timeFormat (dataTime) {
      let time;
      if (!dataTime) {
        // 创建会话没有消息，所以时间为null
        time = '';
      } else {
        // ios无法识别-的时间类型，我们将时间转成类似2021/04/10 19:13:19这种形式，ios和安卓都可以使用
        dataTime = dataTime.replace(new RegExp("-", "gm"), '/');
        // 会话中有时间信息,将时间格式化
        if (new Date().format("yyyy") == new Date(dataTime).format("yyyy")) {
          if (new Date().format("yyyyMMdd") == new Date(dataTime).format("yyyyMMdd")) {
            time = new Date(dataTime).format("hh:mm")
          } else {
            time = new Date(dataTime).format("MM-dd hh:mm")
          }
        } else {
          time = new Date(dataTime).format("yyyy-MM-dd hh:mm")
        }
      }
      return time;
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../styles/dimens';

.dt-chat {
  height: 100%;
  position: relative;

  .dt-title {
    height: $headerHeight;
    padding-top: $statusBarHeight;
    background-color: #5870f8;
  }

  .dt-chat-name {
    width: px2rem(250);
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: px2rem(17);
    font-family: 'SourceHanSansCN-Medium';
    color: #fefefe;
  }

  .dt-content {
    height: calc(100% - #{px2rem(51)} - #{$headerHeight} - #{$statusBarHeight});
    // overflow-y: auto;
    background-color: #edf1f5;
  }

  .dt-content2 {
    height: calc(
      100% - #{px2rem(51)} - #{px2rem(150)} - #{$headerHeight} - #{$statusBarHeight}
    );
    // overflow-y: auto;
    background-color: #edf1f5;
  }

  .dt-refresh {
    height: 100%;
    overflow-y: auto;
  }

  .dt-list {
    background-color: transparent;
  }
  .sendTime {
    text-align: center;
    font-size: 10px;
    color: rgb(146, 146, 146);
  }

  .dt-bottom {
    width: 100%;
    height: px2rem(50);
    background-color: #edf1f5;
    border-top: px2rem(1) solid #d8d8d8;
    position: sticky;
    bottom: 0;

    .dt-input-box {
      display: flex;
      justify-content: center;
      align-items: center;
      height: px2rem(50);

      .dt-input {
        height: 80%;
        width: 95%;
        margin: px2rem(5.5) 0 px2rem(4.5);
        border-radius: px2rem(3);
      }
    }

    .dt-more-box {
      display: flex;
      justify-content: center;
      align-items: center;
      height: px2rem(50);

      .dt-more {
        width: px2rem(30);
        height: px2rem(30);
        margin: px2rem(11) px2rem(12) px2rem(9) px2rem(6);
      }

      .dt-send {
        height: 80%;
        width: 100%;
        text-align: center;
        line-height: px2rem(25);
        white-space: nowrap;
        background-color: #5870f8;
        color: #fff;
        border: none;
        border-radius: px2rem(7);
      }
    }
  }

  .dt-box {
    height: px2rem(150);
    background-color: rgb(249, 249, 249);
  }

  .dt-ico {
    width: px2rem(60);
    height: px2rem(60);
    margin-top: px2rem(40);
    margin-bottom: px2rem(5);
    border-radius: px2rem(10);
    overflow: hidden;
  }
}
</style>